.container {
    padding: 10px;
}

.title {
    display: flex;
    justify-content: center;
}

.title > div {
    font-size: 40px;
    border: 1px solid black;
    border-radius: 15px;
    background-color: cornsilk;
    font-weight: bold;
    width: 60vw;
    height: 70px;
    display: flex;
    justify-content: center;
    align-items: center;
}